* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}
@font-face {
  font-family: "inter-regular";
  src: url("/fonts/Inter_18pt-Regular.ttf");
  font-weight: 400;
}
@font-face {
  font-family: "inter-bold";
  src: url("/fonts/Inter_18pt-Bold.ttf");
  font-weight: 700;
}
@font-face {
  font-family: "clashDisplay-regular";
  src: url("/fonts/ClashDisplay-Regular.ttf");
  font-weight: 400;
}
@font-face {
  font-family: "clashDisplay-medium";
  src: url("/fonts/ClashDisplay-Medium.ttf");
  font-weight: 500;
}
@font-face {
  font-family: "clashDisplay-semibold";
  src: url("/fonts/ClashDisplay-Semibold.ttf");
  font-weight: 600;
}
@font-face {
  font-family: "clashDisplay-bold";
  src: url("/fonts/ClashDisplay-Bold.ttf");
  font-weight: 700;
}
/* font Face = tf  */
/* color face = cl */
/* sizing = ss  */
:root {
  /* Typo graphy  */
  --ff-inter-regular: "inter-regular";
  --ff-inter-bold: "inter-bold";
  --ff-clashDisplay-regular: "clashDisplay-regular";
  --ff-clashDisplay-medium: "clashDisplay-medium";
  --ff-clashDisplay-semibold: "clashDisplay-semibold";
  --ff-clashDisplay-bold: "clashDisplay-bold";
  /* Color  */
  --cf-primary-400: #717342;
  --cf-secondary-400: #f0eee8;
  --cf-neutral-black-900: #000000;
  --cf-neutral-gray-400: #737373;
  --cf-neutral-300: #999999;
  --cf-neutral-200: #e6e6e6;
  --cf-neutral-white-100: #ffffff;
  --cf-error-green: #7da258;
  --cf-error-red: #bd4646;
  /* Size */
  /* pc  */
  /* basic  */
  --ss-global-container: 0% 5%;
  /* margin spacing  */
  --ss-ms-xs: 0.5rem;
  --ss-ms-s: 1rem;
  --ss-ms-r: 1.5rem;
  --ss-ms-l: 3rem;
  --ss-ms-xl: 9rem;
  /* padding spacing  */
  --ss-ps-l: 1.5rem;
  --ss-ps-r: 1rem;
  --ss-ps-s: 0.5rem;
  /* tab  */
  /* margin spacing  */
  --ss-ms-xl-t: 6rem;
  --ss-ms-l-t: 2.5rem;
  /* phone  */
  /* margin spacing  */
  --ss-ms-xl-p: 4rem;
  --ss-ms-l-p: 2rem;
  --ss-ms-r-p: 1.25rem;
  /* padding spacing  */
  --ss-ps-l-p: 1.25rem;
}

/* Utility class */
.primary-gradient {
  background: linear-gradient(180deg, #717342 0%, #717342 43.5%, #959855 100%);
}

/* PC typography  */
.display {
  font-family: var(--ff-clashDisplay-bold);
  color: var(--cf-neutral-white-100);
  font-size: 20.5vw;
  line-height: 100%;
}
h1 {
  font-family: var(--ff-clashDisplay-regular);
  color: var(--cf-primary-400);
  font-size: 5vw;
  line-height: 100%;
}
h2 {
  font-family: var(--ff-clashDisplay-semibold);
  color: var(--cf-primary-400);
  font-size: 3rem;
  line-height: 110%;
}
h3 {
  font-family: var(--ff-clashDisplay-semibold);
  color: var(--cf-neutral-white-100);
  font-size: 1.5rem;
  line-height: 120%;
}
h4 {
  font-family: var(--ff-clashDisplay-semibold);
  color: var(--cf-neutral-black-900);
  font-size: 3.75rem;
  line-height: 100%;
}
p {
  font: var(--ff-inter-regular);
  color: var(--cf-neutral-black-900);
  font-size: 1rem;
  line-height: 120%;
}

.label-1 {
  font-family: var(--ff-clashDisplay-medium);
  color: var(--cf-neutral-black-900);
  font-size: 1rem;
  line-height: 120%;
}
.label-2 {
  font-family: var(--ff-inter-bold);
  color: var(--cf-neutral-black-900);
  font-size: 1rem;
  line-height: 120%;
}
.label-3 {
  font-family: var(--ff-inter-regular);
  color: var(--cf-neutral-black-900);
  font-size: 0.875rem;
  line-height: 100%;
}
.label-4 {
  font-family: var(--ff-clashDisplay-semibold);
  color: var(--cf-primary-400);
  font-size: 8rem;
  opacity: 0.1;
  line-height: 0%;
  transform: translateY(-52px);
}

button {
  background-color: var(--cf-primary-400);
  font-family: var(--ff-clashDisplay-medium);
  color: var(--cf-neutral-white-100);
  font-size: 1rem;
  line-height: 120%;
  border: none;
  padding: 12px 16px;
  border-radius: 8px;
  cursor: pointer;
  margin: 5px;
  transition: 300ms;
}
button:hover {
  -webkit-box-shadow: 0px 5px 0px 0px #e6ea95;
  -moz-box-shadow: 0px 5px 0px 0px #e6ea95;
  box-shadow: 0px 5px 0px 0px #e6ea95;
  transform: translateY(-5px);
}
button:active {
  background-color: var(--cf-neutral-white-100);
  color: var(--cf-primary-400);
  -webkit-box-shadow: 0px 0px 0px 0px var(--cf-neutral-black-900);
  -moz-box-shadow: 0px 0px 0px 0px var(--cf-neutral-black-900);
  box-shadow: 0px 0px 0px 0px var(--cf-neutral-black-900);
  transform: translateY(0px);
  border: 1px solid var(--cf-primary-400);
  transition: 100ms;
}

/* Regular CSS  */
body,
html {
  background-color: var(--cf-secondary-400);
}

/* nav list styleing  */
ul,
li {
  display: inline;
  list-style: none;
  margin-right: var(--ss-ms-s);
}
a {
  color: var(--cf-neutral-white-100);
  text-decoration: none;
  font-family: var(--ff-inter-regular);
}
.container {
  padding: var(--ss-global-container);
}

nav {
  height: min-content;
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-between;
  position: absolute;
  z-index: 4;
  margin-top: -1.5rem;
}
/* nav icon  */
#nav-icon4 {
  display: none;
}
#nav-icon1,
#nav-icon2,
#nav-icon3,
#nav-icon4 {
  width: 60px;
  height: 45px;
  position: relative;
  margin-right: 5%;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: 0.5s ease-in-out;
  -moz-transition: 0.5s ease-in-out;
  -o-transition: 0.5s ease-in-out;
  transition: 0.5s ease-in-out;
  cursor: pointer;
}

#nav-icon1 span,
#nav-icon3 span,
#nav-icon4 span {
  display: block;
  position: absolute;
  height: 6px;
  width: 100%;
  background: var(--cf-neutral-white-100);
  border-radius: 9px;
  opacity: 1;
  left: 0;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: 0.25s ease-in-out;
  -moz-transition: 0.25s ease-in-out;
  -o-transition: 0.25s ease-in-out;
  transition: 0.25s ease-in-out;
}
/* Icon 4 */

#nav-icon4 span:nth-child(1) {
  top: 0px;
  -webkit-transform-origin: left center;
  -moz-transform-origin: left center;
  -o-transform-origin: left center;
  transform-origin: left center;
}

#nav-icon4 span:nth-child(2) {
  top: 18px;
  -webkit-transform-origin: left center;
  -moz-transform-origin: left center;
  -o-transform-origin: left center;
  transform-origin: left center;
}

#nav-icon4 span:nth-child(3) {
  top: 36px;
  -webkit-transform-origin: left center;
  -moz-transform-origin: left center;
  -o-transform-origin: left center;
  transform-origin: left center;
}

#nav-icon4.open span:nth-child(1) {
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  transform: rotate(45deg);
  top: -3px;
  left: 8px;
}

#nav-icon4.open span:nth-child(2) {
  width: 0%;
  opacity: 0;
}

#nav-icon4.open span:nth-child(3) {
  -webkit-transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
  -o-transform: rotate(-45deg);
  transform: rotate(-45deg);
  top: 39px;
  left: 8px;
}

/* nav icon ends */
.background-blur {
  position: absolute;
  z-index: 3;
  height: 15rem;
  width: 100%;
  /* frosted glass BG */
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  background: linear-gradient(
    135deg,
    rgba(255, 255, 255, 0.5),
    rgba(255, 255, 255, 0.1)
  );

  border: 1px solid rgba(255, 255, 255, 0.2);
  -webkit-mask-image: linear-gradient(to bottom, black 10%, transparent 80%);
  mask-image: linear-gradient(to bottom, black 10%, transparent 80%);
}

#png {
  position: absolute;
  z-index: 2;
  width: 100vw;
}
.temp-header .display {
  display: inline-block;
  position: absolute;
  z-index: 1;
  transform: translateY(50%);
  text-align: center;
}
#bg {
  position: relative;
  top: 0;
  width: 100vw;
  background-color: red;
}
.temp-header {
  position: relative;
}
.gradient-transition {
  position: absolute;
  bottom: 0;
  z-index: 4;
  height: 6rem;
  width: 100%;
  background: rgb(240, 238, 232);
  background: -moz-linear-gradient(
    0deg,
    rgba(240, 238, 232, 1) 0%,
    rgba(240, 238, 232, 1) 25%,
    rgba(255, 236, 69, 0) 100%
  );
  background: -webkit-linear-gradient(
    0deg,
    rgba(240, 238, 232, 1) 0%,
    rgba(240, 238, 232, 1) 25%,
    rgba(255, 236, 69, 0) 100%
  );
  background: linear-gradient(
    0deg,
    rgba(240, 238, 232, 1) 0%,
    rgba(240, 238, 232, 1) 25%,
    rgba(255, 236, 69, 0) 100%
  );
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#f0eee8",endColorstr="#ffec45",GradientType=1);
}

/* Header Styling Starts */
header img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.slider {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

.slide-active {
  position: absolute;
  width: 100%;
  height: 100%;
}

.slide-next {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.slide-next .slide-next-img {
  width: 250px;
  height: 350px;
  clip-path: polygon(50% 50%, 50% 50%, 50% 50%, 50% 50%);
}

.slider-content {
  position: absolute;
  left: 2em;
  bottom: 0em;
  width: 100%;
  height: 175px;
  clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 100%);
  overflow: hidden;
}

.slider-content-active {
  position: absolute;
  top: 0;
  left: 0;
  clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 100%);
}

.slider-content-next {
  position: absolute;
  top: 200px;
  left: 0;
  clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 100%);
}

header h3 {
  text-transform: uppercase;
  font-family: var(--ff-clashDisplay-bold);
  font-size: 150px;
  font-weight: lighter;
  color: #fff;
  z-index: 6;
}

h3 span {
  position: relative;
}

/* Animation */
/* text animation  */
.text {
  text-shadow: 0 1.1em 0 currentcolor;
  display: inline-block;
  overflow: hidden;
}
/* Responsive settings  */
@media (max-width: 1200px) {
  header h3 {
    font-size: 100px;
  }
  .slider-content {
    height: 120px;
  }
}
@media (max-width: 834px) {
  :root {
    /* size */
    /* basic  */
    --ss-global-container: 0% 2%;
    /* margin  */
    --ss-ms-xl: var(--ss-ms-xl-t);
    --ss-ms-l: var(--ss-ms-l-t);
  }
  header h3 {
    font-size: 80px;
  }
  .slider-content {
    height: 100px;
  }
  h2 {
    font-size: 3rem;
  }

  .label-4 {
    font-size: 6rem;
    transform: translateY(-42px);
  }
  /* nav menu  */
  /* Hidden by default */
  .nav-menu {
    position: fixed;
    top: -100%;
    left: 0;
    height: 100vh;
    width: 100%;
    border-radius: 0 0 16px 16px;
    transition: top 0.7s ease-in-out;
    z-index: 10;

    display: grid;
    place-content: center;
    background-color: #d4d973;
    border-radius: 0px 0px 16px 16px;
  }

  .nav-active {
    top: 0;
  }

  .nav-menu ul li {
    display: block;
    text-align: center;
    font-size: 4rem;
  }
  .nav-menu ul li a {
    font-family: var(--ff-clashDisplay-medium);
  }
  .nav-menu ul {
    margin-bottom: 3rem;
  }
  #nav-icon4 {
    display: block;
    z-index: 11;
  }
}

@media (max-width: 430px) {
  :root {
    /* size */
    /* margin  */
    --ss-ms-xl: var(--ss-ms-xl-p);
    --ss-ms-l: var(--ss-ms-l-p);
    --ss-ms-r: var(--ss-ms-r-p);
    /* padding */
    --ss-ps-l: var(--ss-ps-l-p);
  }
  header h3 {
    font-size: 40px;
  }
  .slider-content {
    height: 80px;
    left: 0.5em;
    width: 95vw;
    overflow: hidden;
  }
  h2 {
    font-size: 2rem;
  }
  h3 {
    font-size: 1.25rem;
  }
  h4 {
    font-size: 1.5rem;
  }
  p {
    font-size: 0.875rem;
  }
  .label-1 {
    font-size: 0.875rem;
  }
  .label-3 {
    font-size: 0.75rem;
  }
  .label-4 {
    font-size: 3rem;
    transform: translateY(-23px);
  }
  #main-logo {
    margin-left: -2rem;
  }
  #nav-icon4 {
    margin-right: 2%;
  }

  #nav-icon4,
  #main-logo {
    scale: 0.8;
  }
}
